@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
  #app{
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

@media (max-width: 1000px){
  #app{
      flex-direction: column;
  }

  #app main{
      margin-left: 0;
      width: 100%;
  }

  #app aside{
      width: 100%;
  }
}
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root, #app{
    height: 100%;
}

body{
    background: #E1E1E1;
}

body, input, button{
    font-family: Roboto, sans-serif;
}
aside{
    width: 320px;
    height: 100vh;
    background: #434343;
    color: #FFF;
    padding-top: 30px;
    text-shadow: 2px 2px black ;
}

aside strong{
    padding: 30px 60px;
    font-size: 34px;
    text-align: left;
    color: #FFF;
}

aside .title{
    text-decoration: none;
}
main{
    flex: 1 1;
    height: 90vh;
    padding: 30px 75px;
}

span#title{
    font-size: 36px;
}
.menu{
    height: auto;
    margin-top: 30px;
}

.menu-option{
    font-size: 20px;
    padding: 10px 40px;
    border-style: solid;
    border-width: 1px 0px;
    border-color: #707070;
    display: block;
    color: #FFF;
    text-decoration: none;
}

.menu-option + .menu-option{
    border-top: 0;
}

.menu-option:hover{
    background-color: #303030;
    cursor: pointer;
}
.script-box{
    margin-top: 28px;
    height: 85vh;
    border: 1px solid black;
    background-color: white;
    text-align: center;
}

.script-box input{
    margin-top: 1vh;
    padding: 0 2vw;
    width: 95%;
    height: 30px;
    border-radius: 20vh;
    border-color: #adadad;
    border-style: outset;
}

table{
    width: 95%;
    height: 8vh;
    text-align: left;
    margin: 15px 2vw 2vh 2vw;
    border-collapse: collapse;
}

th{
    text-align: center;
}

th, td{
    border-bottom: 1px solid #ddd;
    padding: 1vh;
}

tr + tr:hover{
    background-color:#f5f5f5;
}
button{
    border: none;
    background-color: white;
}

.option{
    text-align: center;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.modal-main {
    position: fixed;
    background: white;
    width: 80%;
    height: 80vh;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}

.close-button{
    padding-right: 0.3vw;
    font-size: 30px;
    position: absolute;
    top: 0;
    right: 0;  
}

.name-author{
    display: flex;
    padding-top: 2vh;
    padding-left: 2%;
    padding-right: 2%;
    width: 100%;
}

.name, .author{
    width: 50%;
}

.name-author label, .content label{
    font-size: 2vh;
    font-weight: 600;
}

.name-author input{
    font-size: 1.7vh;
    height: 3vh;
    display: flex;
    width: 90%;    
    border-width: 1px;
    border-color: #434343;
    border-style: solid;
}
.content{
    position: relative;
    padding-top: 1vh;
    padding-left: 2%;
    padding-right: 2%;
}

.content .button-register{
    right: 3vw;
}

.content .ReactCodeMirror{
    height: 69.1vh;
}

.content .CodeMirror{
    height: 69vh;
    border-width: 1px;
    border-color: #434343;
    border-style: solid;
}
.code-editor{
    position: absolute;
    margin-top: 28px;
    height: 85vh;
    width: 75vw;
    border: 1px solid black;
    background-color: white;
}

.code-editor .ReactCodeMirror{
    height: 85vh;
}

.code-editor .CodeMirror{
    height: 80.7vh;
}

.input-block{
    margin-top: 1vh;
    text-align: center;
    height: 3vh;
}

.input-block input{
    height: 100%;
    width: 45%;
    border-radius: 20vh;
    padding: 0 2vw;
    border-color: #adadad;
    border-style: outset;
}

.input-block input + input{
    margin-left: 2%;
}

.button-register{
    position: absolute;
    background-color: #434343;
    color: white;
    padding: 1vh 1vw;
    bottom: 2vh;
    right: 1vw;
    font-weight: 800;
    border: solid 2px #272727;
}

.button-register:hover{
    background-color: #303030;
    cursor: pointer;
}
